
import Radio from 'vux/src/components/radio/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Loading from 'vux/src/components/loading/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'

import {
CellGroup,
Icon,
NavBar,
NoticeBar,
Swipe,
SwipeItem,
Row,
Col,
List
} from "vant";
import { debug } from "util";

export default {
directives: {},
components: {
[CellGroup.name]: CellGroup,
[Icon.name]: Icon,
[NavBar.name]: NavBar,
[NoticeBar.name]: NoticeBar,
[Swipe.name]: Swipe,
[SwipeItem.name]: SwipeItem,
[Row.name]: Row,
[Col.name]: Col,
[List.name]: List,

Radio,
Group,
Cell,
Badge,
Drawer,
ButtonTab,
ButtonTabItem,
ViewBox,
XHeader,
Tabbar,
TabbarItem,
Loading,
Actionsheet,
Grid,
GridItem,
Flexbox,
FlexboxItem,
XButton
},
data() {
return {
title: "财务报账系统",
functionTitle: "TITLE",
isShowBar: true,
isShowFunction: true,
isShowProcess: false,
isShowMy: false,
isShowRouterView: false,
images: [
"https://picsum.photos/400/180/?image=1",
"https://picsum.photos/400/180/?image=2",
"https://picsum.photos/400/180/?image=3",
"https://picsum.photos/400/180/?image=4"
],
functionList: [],
isLoading: false,
path: "",
entryUrl: document.location.href,
showMenu: false,
menus: {
"language.noop": '<span class="menu-title">角色切换</span>',
"zh-CN": "管理员",
en: "部门领导"
},
drawerVisibility: false,
showMode: "push",
showModeValue: "push",
showPlacement: "left",
showPlacementValue: "left"
};
},
methods: {
loginOut() {
sessionStorage.setItem("userName", "");
sessionStorage.setItem("userToken", ""); 
this.$router.push({
name: "login_page"
});
},
onItemClickImg() {
this.$router.push({
name: "feedback-page"
});
},
feedbackClick() {
this.drawerVisibility = false;
this.$router.push({
name: "feedback-page"
});
},
onItemClickFunction() {
this.isShowFunction = true;
this.isShowProcess = false;
this.isShowMy = false;
},
onItemClickProcess() {
this.isShowFunction = false;
this.isShowProcess = true;
this.isShowMy = false;
},
onItemClickMy() {
this.isShowFunction = false;
this.isShowProcess = false;
this.isShowMy = true;
},
onShowModeChange(val) {
/** hide drawer before changing showMode **/
this.drawerVisibility = false;
setTimeout(one => {
this.showModeValue = val;
}, 400);
},
onPlacementChange(val) {
/** hide drawer before changing position **/
this.drawerVisibility = false;
setTimeout(one => {
this.showPlacementValue = val;
}, 400);
},
onClickMore() {
this.showMenu = true;
},
changeLocale(locale) {
this.$i18n.set(locale);
this.$locale.set(locale);
}

},
mounted() {






},
beforeDestroy() {
this.box && this.box.removeEventListener("scroll", this.handler, false);
},
created() {
this.functionList = [
{
title: "我的申请",
icon: "/static/icon_ehr_tool_blood_pressure.png"
},
{
title: "待审批",
icon: "/static/icon_ehr_tool_body_temerature.png"
},
{
title: "已审批",
icon: "/static/icon_ehr_tool_diet.png"
},
{
title: "已提交",
icon: "/static/icon_ehr_tool_glucose.png"
},
{
title: "草稿单",
icon: "/static/icon_ehr_tool_heart_rate.png"
},
{
title: "作废单",
icon: "/static/icon_ehr_tool_sport.png"
}
];
},

computed: {















isShowNav() {
if (this.entryUrl.indexOf("hide-nav") > -1) {
return false;
}
return true;
},
leftOptions() {
return {

};
},
rightOptions() {
return {
showMore: true
};
},
headerTransition() {},
componentName() {




},
isDemo() {

},
isTabbarDemo() {

},






viewTransition() {}
},
watch: {
$route(to, from) {
if (to.path != "/") {
this.isShowBar = false;
this.isShowFunction = false;
this.isShowProcess = false;
this.isShowMy = false;
this.isShowRouterView = true;
} else {
this.functionTitle = to.meta;
this.isShowBar = true;
this.isShowFunction = true;
this.isShowProcess = false;
this.isShowMy = false;
this.isShowRouterView = false;
}
}
}
};



